<template>
  <div class>
    <LunBo :dataimg="dataimg"></LunBo>
    <div
      class="Video"
      v-for="(item, index) in videoClassify.slice(-1)"
      :key="'Video2' + index"
    >
      <div class="top">
        <span class="text1 darkest-text">{{ item.title }}</span>
        <a
          href="javascript:void(0)"
          class="text2 theme-text"
          v-on:click.stop="lookMore(item, index)"
          >查看更多</a
        >
      </div>
      <div class="bottom lightest-bg">
        <div class="video-list">
          <div
            class="myVideo"
            v-for="(index, i) in (item.showList || '').slice(0, 6)"
            :key="i"
          >
            <img
              :src="
                index.images ? index.images.appdetailsmall : defaultPcBackImg
              "
              class="title"
            />

            <a href="javascript:void(0)" @click="goVideo(index, item.showList)">
              <span
                class="iconfont icon-bofang lightest-text"
                id="play-picture"
              ></span>
            </a>
            <p class="text-1 dark-text">{{ index.title }}</p>
            <p class="text-2 light-text">{{ index.categoryTitle }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import LunBo from "../../components/lunbo";
import advertisement from "../../components/advertising-tab";
// import videoList from "../../pages/video/components/videoList";

export default {
  data() {
    return {
      videoList: "",
      videoClassify: "",
      dataimg: [
        {
          src: require("../../assets/img/vlog1.png")
        },
        {
          src: require("../../assets/img/lbvideo3.png")
        },
        {
          src: require("../../assets/img/lbvideo2.png")
        }
      ]
    };
  },
  mounted() {
    var _this = this;
    var url = this.APIURL + "/classic/video/category";
    this.$axios.post(url).then(function({ data }) {
      _this.videoClassify = data.data;
      _this.videoClassify.map(val => {
        _this.getData(val).then(data => {
          // console.log(data)
          val.list = data.slice(0, 3);
          val.isShowAll = false;
          val.showList = data;
          // _this.videoList=val
          // console.log( _this.videoList)
          val = Object.assign({}, val);
          _this.videoClassify = [].concat(_this.videoClassify);
        });
        return val;
      });
    });
  },
  methods: {
    async getData(val) {
      let dataList = [];
      var url = this.APIURL + "/classic/video/all";
      try {
        let res = await this.$axios.post(url, {
          category: val.id,
          pageSize: 100
        });
        dataList = res.data.data.dataSet;
      } catch (err) {
        console.log(err);
      }
      return dataList;
    },
    //点击跳转
    goVideo(index, list) {
      console.log(index, list);
      var _this = this;
      _this.videoList = list;
      this.$router.push({
        path: "/video/details"
      });
      // localStorage.setItem('videoId',index.id)

      this.$store.commit("setVideo", index);
      this.$store.commit("setVideoList", this.videoList);
    },
    //点击查看更多
    lookMore(item) {
      // console.log(item);
      this.$router.push("/vlog/vlogmore");
      this.$store.commit("setVideoList", item.showList);
    }
  },
  computed: {},

  watch: {},
  components: {
    LunBo,
    advertisement
    // videoList
    //  video_template
  }
};
</script>
<style lang="scss"></style>
